import { BiPlugin } from '../../../types';
import getFonts from '../queries/getFonts';
import { DEPRECATED_ACTIONS, actions, isActions } from '../actions';
import onAction from '../handlers/onAction';

const queries = {
  getFonts,
};

function createFontsPlugin(): BiPlugin {
  return {
    queries,
    onAction,
    actions,
    isActions,
    models: {
      klass: 'mark',
      type: 'fonts',
    },
  };
}

createFontsPlugin.actions = DEPRECATED_ACTIONS;
createFontsPlugin.queries = queries;

export default createFontsPlugin;
